<template>
  <div class="user">
    <!-- 头部 -->
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>数据管理</el-breadcrumb-item>
        <el-breadcrumb-item>商家列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 数据列表 -->
    <div class="cartlist">
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="店铺名称">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="店铺地址">
                <span>{{ props.row.address }}</span>
              </el-form-item>
              <el-form-item label="店铺介绍">
                <span>{{ props.row.description }}</span>
              </el-form-item>
              <el-form-item label="店铺 ID">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="联系电话">
                <span>{{ props.row.phone }}</span>
              </el-form-item>
              <el-form-item label="评分">
                <span>{{ props.row.rating }}</span>
              </el-form-item>
              <el-form-item label="销售量">
                <span>{{ props.row.recent_order_num }}</span>
              </el-form-item>
              <el-form-item label="分类">
                <span>{{ props.row.category }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="店铺名称"> </el-table-column>
        <el-table-column prop="address" label="店铺地址"> </el-table-column>
        <el-table-column prop="description" label="店铺介绍"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="editCart(scope.row)">编辑</el-button>
            <el-button size="mini" @click="$router.push('/home/addshoplist')">添加商品</el-button>
            <el-button size="mini" type="danger" @click="delData()">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="cartList.latitu"
        :page-size="cartList.offset"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
      <!-- 编辑弹框 -->
      <el-dialog title="修改店铺信息" :visible.sync="Editdialog" width="45%">
        <el-form ref="form" label-width="80px">
          <el-form-item label="店铺名称">
            <el-input v-model="username"></el-input>
          </el-form-item>
          <el-form-item label="详细地址">
            <el-input v-model="addre"></el-input>
            <span>当前城市：北京</span>
          </el-form-item>
          <el-form-item label="店铺介绍" width="100">
            <el-input v-model="introduce"></el-input>
          </el-form-item>
          <el-form-item label="联系电话">
            <el-input v-model="call"></el-input>
          </el-form-item>
          <el-form-item label="店铺分类">
            <el-cascader :options="options"></el-cascader>
          </el-form-item>
          <el-form-item label="商品图片">
            
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="Editdialog = false">取 消</el-button>
          <el-button type="primary" @click="editCartList('editCartForm')"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getShopCount } from "../api/userList";
export default {
  data() {
    return {
      tableData: [],
      cartList: {
        latitu: 1,
        offset: 0,
        limit: 10,
      },
      Editdialog: false,
      id: "",
      username: "",
      addre: "",
      introduce: "",
      call: "",
      image:'',
      // editList: [],
      options: [
        {
          value: "zhinan",
          label: "指南",
          children: [
            {
              value: "shejiyuanze",
              label: "设计原则",
            },
            {
              value: "daohang",
              label: "导航",
            },
          ],
        },
        {
          value: "zujian",
          label: "组件",
          children: [
            {
              value: "basic",
              label: "Basic",
            },
            {
              value: "form",
              label: "Form",
            },
            {
              value: "notice",
              label: "Notice",
            },
            {
              value: "navigation",
              label: "Navigation",
            },
            {
              value: "others",
              label: "Others",
            },
          ],
        },
        {
          value: "ziyuan",
          label: "资源",
          children: [
            {
              value: "axure",
              label: "Axure Components",
            },
            {
              value: "sketch",
              label: "Sketch Templates",
            },
            {
              value: "jiaohu",
              label: "组件交互文档",
            },
          ],
        },
      ],
      imageUrl: "",
      total: 0,
      fits: ["fill", "contain", "cover", "none", "scale-down"],
    };
  },
  created() {},
  mounted() {
    // this.UserShopList();
    this.initData();
  },
  methods: {
    // 商品列表的数据
    async UserShopList() {
      let { data: res } = await this.$http.get(
        `/shopping/restaurants?latitude=39.90469&longitude=116.407173&offset=${this.cartList.offset}&limit=${this.cartList.limit}`
      );
      // console.log(res);
      this.tableData = res;
    },
    // 修改的弹框
    editCart(arr) {
      this.Editdialog = true;
      this.id = arr.id;
      this.username = arr.name;
      this.introduce = arr.description;
      this.addre = arr.address;
      this.call = arr.phone;
      this.image = arr.image_path
    },
    // 修改数据写的接口
    async editCartList() {
      let { data: res } = await this.$http.post(
        `/shopping/updateshop?id=${this.id}`,
        {
          name: this.username,
          address: this.addre,
          description: this.introduce,
          phone: this.call,
          image_path:this.image
        }
      );
      console.log(res);
      this.Editdialog = false;
      this.UserShopList();
    },
    // 分页
    handleSizeChange(size) {
      console.log(`每页${size}条`);
    },
    handleCurrentChange(page) {
      this.cartList.latitu = page;
      this.cartList.offset = (page - 1) * this.cartList.limit;
      this.UserShopList();
    },
    // 数据的总页数
    async initData() {
      try {
        let { data: countData } = await getShopCount(this.offset, this.limit);
        // cosnole.log(countData)
        if (countData.status == 1) {
          this.total = countData.count;
        } else {
          console.log("获取数据失败");
        }
        this.UserShopList();
      } catch (err) {
        console.log("获取数据失败");
      }
    },
    // 删除
    delData(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  },
};
</script>

<style scoped>
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.cartlist {
  padding: 20px;
}
.demo-table-expand {
  font-size: 0;
}
.el-form-item__label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
