<template>
  <div>
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>添加数据</el-breadcrumb-item>
        <el-breadcrumb-item>添加商铺</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown @click="logout()">
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="$router.push('/home/homepage')"
            >首页</el-dropdown-item
          >
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-row style="margin-top: 20px;">
      <el-col :span="12" :offset="4">
        <el-form
          :model="formData"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="活动名称" prop="name">
            <el-input v-model="formData.name"></el-input>
          </el-form-item>
          <el-form-item label="详细地址" prop="address">
            <el-autocomplete v-model="formData.address" placeholder="请输入地址" style="width: 100%;"></el-autocomplete>
            <span>当前城市：</span>
          </el-form-item>
          <el-form-item label="联系电话" prop="phone">
            <el-input v-model="formData.phone"></el-input>
          </el-form-item>
          <el-form-item label="店铺简介" prop="description">
            <el-input v-model="formData.description"></el-input>
          </el-form-item>
          <el-form-item label="店铺标语" prop="promotion_info">
            <el-autocomplete
              v-model="formData.address"
              :fetch-suggestions="querySearchAsync"
              placeholder="请输入地址"
              style="width: 100%;"
              @select="addressSelect"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item label="店铺分类">
            <el-cascader :options="categoryOptions" v-model="selectedCategory" change-on-select></el-cascader>
          </el-form-item>
          <el-form-item label="店铺特点" style="white-space: nowrap;">
            <span>品牌保证</span>
            <el-switch on-text off-text v-model="formData.is_premium"></el-switch>
            <span>蜂鸟专送</span>
            <el-switch on-text off-text v-model="formData.delivery_mode"></el-switch>
            <span>新开店铺</span>
            <el-switch on-text off-text v-model="formData.new"></el-switch>
          </el-form-item>
          <el-form-item style="white-space: nowrap;">
            <span>外卖保</span>
            <el-switch on-text off-text v-model="formData.bao"></el-switch>
            <span>准时达</span>
            <el-switch on-text off-text v-model="formData.zhun"></el-switch>
            <span>开发票</span>
            <el-switch on-text off-text v-model="formData.piao"></el-switch>
          </el-form-item>
          <el-form-item label="配送费" prop="float_delivery_fee">
            <el-input-number v-model="formData.float_delivery_fee" :min="0" :max="20"></el-input-number>
          </el-form-item>
          <el-form-item label="起送价" prop="float_minimum_order_amount">
            <el-input-number v-model="formData.float_minimum_order_amount" :min="0" :max="100"></el-input-number>
          </el-form-item>
          <el-form-item label="营业时间" style="white-space: nowrap;">
            <el-col :span="8">
              <el-time-select
                placeholder="起始时间"
                v-model="formData.startTime"
                :picker-options="{
							start: '05:30',
							step: '00:15',
							end: '23:30'
							}"
              ></el-time-select>
            </el-col>
            <el-col class="line" :span="2">——</el-col>
            <el-col :span="8">
              <el-time-select
                placeholder="结束时间"
                v-model="formData.endTime"
                :picker-options="{
							start: '05:30',
							step: '00:15',
							end: '23:30',
							minTime: formData.startTime
							}"
              ></el-time-select>
            </el-col>
          </el-form-item>

          <el-form-item label="上传店铺头像">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="上传营业执照">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="上传餐饮服务许可证">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="优惠活动">
            <el-select
              v-model="activityValue"
              @change="selectActivity"
              :placeholder="activityValue"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-table
            border
            :header-cell-style="{background:'#eef1f6',color:'#000000'}"
            style="min-width: 600px;margin-bottom: 20px;"
            align="cneter"
          >
            <el-table-column prop="icon_name" label="活动标题" align="cneter" width="120"></el-table-column>
            <el-table-column prop="name" label="活动名称" align="cneter" width="120"></el-table-column>
            <el-table-column prop="description" align="cneter" label="活动详情"></el-table-column>
            <el-table-column label="操作" width="120">
              <template>
                <el-button size="small" type="danger">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-form-item class="button_submit">
            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      formData: {
        is_premium: true,
        delivery_mode: true,
        new: true,
        bao: true,
        zhun: true,
        piao: true,
        value1: true,
        selectActivity: true,
        float_delivery_fee: 5, //运费
        float_minimum_order_amount: 20, //起价
        startTime: "",
        endTime: "",
        imageUrl: "",
        address: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        addlist:[]
      },
      selectedCategory: ["快餐便当", "简餐"],
      categoryOptions: [],
      rules: {
        name: [{ required: true, message: "请输入店铺名称", trigger: "blur" }],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "请输入联系电话" },
          { type: "number", message: "电话号码必须是数字" }
        ]
      },
      options: [
        {
          value: "满减优惠",
          label: "满减优惠"
        },
        {
          value: "优惠大酬宾",
          label: "优惠大酬宾"
        },
        {
          value: "新用户立减",
          label: "新用户立减"
        },
        {
          value: "进店领券",
          label: "进店领券"
        }
      ],
      activityValue: "满减优惠",
      activities: [
        {
          icon_name: "减",
          name: "满减优惠",
          description: "满30减5，满60减8"
        }
      ]
    };
  },
  mounted() {
    this.getdatas();
  },
  computed: {},
  methods: {
    //获取本地JSON数据并渲染
    getdatas() {
      this.$axios.get("/shopping/v2/restaurant/category").then(res => {
        console.log(res);

        this.addlist = res.data;
      });
    },

    submitForm(formName){
      this.$refs[formName].validate(async valid=>{
        if(valid){

        }
      })
    },
    

    //图片代码
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style lang="scss" scoped>
.button_submit {
  text-align: center;
}

.avatar-uploader {
  width: 120px;
  border: 1px dashed #d9d9d980;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  height: 120px;
  display: block;
}
.el-table .info-row {
  background: #c9e5f5;
}

.el-table .positive-row {
  background: #e2f0e4;
}
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
</style>
