<template>
  <div class="image-list">
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>图表</el-breadcrumb-item>
        <el-breadcrumb-item>用户分布</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown @click="logout()">
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 用户图表分布 -->
    <div class="text item">
      <div id="echart"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
     // 基于准备好的dom，初始化echarts实例
    let myChart = this.$echarts.init(document.getElementById("echart")); // echartsExample是显示echart的元素id
    // 绘制图表
    myChart.setOption({
      title: {
        text: "用户分布",
        left: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      legend: {
        orient: "vertical",
        left: "left",
        data: ["北京", "上海", "深圳", "杭州", "其他"]
      },
      series: [
        {
          name: "访问来源",
          type: "pie",
          radius: "55%",
          center: ["50%", "60%"],
          data: [
            { value: 8457, name: "北京" },
            { value: 3656, name: "上海" },
            { value: 4031, name: "深圳" },
            { value: 2161, name: "杭州" },
            { value: 29942, name: "其他" }
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    });
  },
  methods: {
    
  },
};
</script>

<style scoped>
.image-list{
    width: 100%;
    height: 100%;
}
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.text{
    width: 100%;
    height: 100%;
}
#echart{
    top: 20px;
    left: 100px;
    width: 70%;
    height: 400px;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-breadcrumb-item {
  font-size: 13px;
}
</style>
