<template>
  <div class="user">
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>数据管理</el-breadcrumb-item>
        <el-breadcrumb-item>订单列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 获取订单列表数据 -->
    <div class="order-list">
      <el-table :data="orderData" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="用户名">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="店铺名称">
                <span>{{ props.row.shop }}</span>
              </el-form-item>
              <el-form-item label="收货地址">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="店铺 ID">
                <span>{{ props.row.restaurant_id }}</span>
              </el-form-item>
              <el-form-item label="店铺地址">
                <span>{{ props.row.category }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="id" label="订单ID"> </el-table-column>
        <el-table-column prop="total_amount" label="总价格"> </el-table-column>
        <el-table-column prop="status_bar.title" label="订单状态">
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        background
        :page-size="10"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {getOrderList} from '../api/OrderLlist'
export default {
  data() {
    return {
      orderData: [],
      pageNum: 1,
      offset: 0,
      limit: 10,
      total: 0,
    };
  },
  created() {
    this.restaurant_id = this.$route.query.restaurant_id;
  },
  mounted() {
    this.getOrderList();
    this.initOrder()
  },
  methods: {
    async getOrderList() {
      let { data: res } = await this.$http.get(
        `/bos/orders?offset=${this.offset}&limit=${this.limit}`
      );
      console.log(res);
      this.orderData = res;
    },
    //分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.offset = (val - 1)*this.limit
      this.getOrderList();
    },
    // 订单列表的总数据
    async initOrder(){
      try{
        let {data:countData} = await getOrderList()
        if(countData.status == 1){
          this.total = countData.count
        }else{
          console.log('获取用户失败')
        }
        this.getOrderList();
      }catch(err){
        console.log('获取数据失败')
      }
    }
  },
};
</script>

<style scoped>
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.order-list {
  padding: 20px;
}
.el-pagination {
  margin-top: 10px;
}
</style>
