<template>
      
  <div class="index">
    <el-header>
      <el-breadcrumb>
        <el-breadcrumb-item>首页</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown @click="logout()">
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item @click.native="logout()">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <div class="addlist">
      <h5>数据统计</h5>
      <div class="btn-list">
        <p class="sp1">当日数据：</p>
        <p class="sp2">
          <span class="span1">8</span>
          <span class="span2">新增用户</span>
        </p>
        <p class="sp2">
          <span class="span1">1</span>
          <span class="span2">新增订单</span>
        </p>
        <p class="sp2">
          <span class="span1">30</span>
          <span class="span2">新增管理员</span>
        </p>
      </div>
      <div class="btn-list">
        <p class="sp3">总数据：</p>
        <p class="sp2">
          <span class="span1">52520</span>
          <span class="span2">注册用户</span>
        </p>
        <p class="sp2">
          <span class="span1">19648</span>
          <span class="span2">订单</span>
        </p>
        <p class="sp2">
          <span class="span1">81502</span>
          <span class="span2">管理员</span>
        </p>
      </div>
    </div>
    <div class="text item">
      <div id="echart" style="width: 600px; height: 368px"></div>
    </div>
        
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
      this.setEcharts()
  },
  methods: {
    setEcharts() {
      let myechart = this.$echarts.init(document.getElementById("echart"));
      let option = {
        title: {
          text: "走势图",
          subtext: "用户",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["新注册用户", "新增订单", "新增管理员"],
        },
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: "none",
            },
            dataView: { readOnly: false },
            magicType: { type: ["line", "bar"] },
            restore: {},
            saveAsImage: {},
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: "{value}",
          },
        },
        series: [
          {
            name: "新注册用户",
            type: "line",
            data: [22, 55, 89, 75, 63, 36, 52],
            markPoint: {
              data: [
                { type: "max", name: "最大值" },
                { type: "min", name: "最小值" },
              ],
            },
            markLine: {
              data: [{ type: "average", name: "平均值" }],
            },
          },
          {
            name: "新增订单",
            type: "line",
            data: [22, 25, 29, 85, 15, 96, 25],
            markPoint: {
              data: [{ name: "周最低", value: -2, xAxis: 1, yAxis: -1.5 }],
            },
            markLine: {
              data: [
                { type: "average", name: "平均值" },
                [
                  {
                    symbol: "none",
                    x: "90%",
                    yAxis: "max",
                  },
                  {
                    symbol: "circle",
                    label: {
                      position: "start",
                      formatter: "最大值",
                    },
                    type: "max",
                    name: "最高点",
                  },
                ],
              ],
            },
          },
          {
            name: "新增管理员",
            type: "line",
            data: [75, 85, 53, 25, 63, 45, 22],
            markPoint: {
              data: [{ name: "周最低", value: -2, xAxis: 1, yAxis: -1.5 }],
            },
            markLine: {
              data: [
                { type: "average", name: "平均值" },
                [
                  {
                    symbol: "none",
                    x: "90%",
                    yAxis: "max",
                  },
                  {
                    symbol: "circle",
                    label: {
                      position: "start",
                      formatter: "最大值",
                    },
                    type: "max",
                    name: "最高点",
                  },
                ],
              ],
            },
          },
        ],
      };
      myechart.setOption(option);
    },
    logout(){
      this.$confirm('您是否退出登录?', '确定退出', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          this.$message({
            type: 'success',
            message: '退出成功!'
          });
          sessionStorage.removeItem('status')
          this.$router.push('/')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });          
        });
    }
  },
};
</script>

<style scoped lang="scss">
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.text{
    width: 100%;
}
#echart{
    margin-left: 100px;
    margin-top: 100px;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.addlist {
  width: 100%;
  margin-top: 20px;
  h5 {
    font-size: 25px;
    text-align: center;
  }
}
.btn-list {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  .sp1 {
    width: 170px;
    height: 35px;
    background: #ff9800;
    color: #fff;
    border-radius: 10px;
    line-height: 35px;
    text-align: center;
    font-size: 20px;
    margin-left: 20px;
  }
  .sp2 {
    width: 180px;
    height: 35px;
    background: #e5e9f2;
    line-height: 35px;
    border-radius: 10px;
    text-align: center;
    margin: 10px;
    .span1 {
      font-size: 23px;
      margin-right: 5px;
      color: #000;
    }
    .span2 {
      font-size: 13px;
      color: #666666;
    }
  }
  .sp3 {
    width: 170px;
    height: 35px;
    background: #20a0ff;
    color: #fff;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    border-radius: 10px;
    margin-left: 20px;
  }
}
</style>
