<template>
  <div class="user">
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>数据管理</el-breadcrumb-item>
        <el-breadcrumb-item>食品列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 食品列表数据 -->
    <div class="food-list">
      <el-table :data="FoodList" border style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="食品名称：">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="餐馆名称：">
                <span>{{ props.row.shop }}</span>
              </el-form-item>
              <el-form-item label="食品 ID：">
                <span>{{ props.row.item_id }}</span>
              </el-form-item>
              <el-form-item label="餐馆 ID：">
                <span>{{ props.row.restaurant_id }}</span>
              </el-form-item>
              <el-form-item label="食品介绍：">
                <span>{{ props.row.description }}</span>
              </el-form-item>
              <el-form-item label="餐馆地址：">
                <span>{{ props.row.address }}</span>
              </el-form-item>
              <el-form-item label="食品评分：">
                <span>{{ props.row.rating }}</span>
              </el-form-item>
              <el-form-item label="食品分类：">
                <span>{{ props.row.desc }}</span>
              </el-form-item>
              <el-form-item label="月销量：">
                <span>{{ props.row.month_sales }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="食品名称"></el-table-column>
        <el-table-column prop="description" label="食品介绍"></el-table-column>
        <el-table-column prop="rating" label="评分"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini">编辑</el-button>
            <el-button type="danger" size="mini" @click="ajaxDelete(scope.row.food_id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        :page-size="10"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {delFood} from '../api/FoodList'
import {getFoodCount} from '../api/userList'
export default {
  data() {
    return {
      FoodList: [],
      pageNum: 1,
      limit: 10,
      offset:0,
      total:0,
      restaurant_id:null
    };
  },
  created() {},
  mounted() {
    this.ajaxFoodList();
    this.initFood()
    this.ajaxDelete()
  },
  methods: {
    // 食品列表数据
    async ajaxFoodList() {
      let { data: res } = await this.$http.get(
        `/shopping/v2/foods?offset=${this.offset}&limit=${this.limit}&restaurant_id=${this.restaurant_id}`
      );
      // console.log(res);
      this.FoodList = res;
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页${val}条`)
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.offset = (val - 1)*this.limit
      this.ajaxFoodList()
    },
    // 获取食品列表的总条数
    async initFood(){
      try{
        let {data:countData} = await getFoodCount()
        if(countData.status == 1){
          this.total = countData.count
        }else{
          this.$message.error('获取数据失败')
        }
        this.ajaxFoodList()
      }catch(err){
        console.log('获取数据失败')
      }
    },
    // 删除接口
    async ajaxDelete(index,row){
       try{
         let {data:del} = await delFood(row.item_id)
         console.log(del)
         if(del.status == 1){
           this.$message({
             type:'success',
             message:'删除食品成功',
             duration:1000
           })
           this.ajaxFoodList();
           this.FoodList.splice(index,1)
         }else{
           this.$message.error('删除数据失败')
         }
       }catch(err){
         console.log('获取数据失败')
       }
    }
  },
};
</script>

<style scoped>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.food-list {
  padding: 20px;
}
.el-pagination{
  margin-top: 10px;
}
</style>
