import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
// 首页
import HomePage from '../HomePage/Index.vue'

// 数据管理页面
import UserList from '../PageList/UserList.vue'
import CartList from '../PageList/CartList.vue'
import FoodList from '../PageList/FoodList.vue'
import OrderList from '../PageList/OrderList.vue'
import MemberList from '../PageList/MemberList.vue'

// 图表页面路由
import ImageList from '../ImagePage/ImageList.vue'

//文本编辑页面路由
import UpdateList from '../UpdataPage/UpdataList.vue'

// 添加商铺页面路由
import AddShop from '../AddShop/addshop.vue'
import addshoplist from '../AddShop/addshoplist.vue'

// 说明页面
import Explain from '../state/Explain.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home,
    beforeEnter(to,from,next){
      if(sessionStorage.getItem('status')){
        next()
      }else{
        next('/')
      }
    },
    children:[
      {
        path:'/home/homepage',
        name:'HomePage',
        component:HomePage
      },
      {
        path:'/',
        redirect: '/home/homepage'
      },
      // 数据管理页面
      {
        path:'/home/userlist',
        name:'UserList',
        component:UserList
      },
      {
        path:'/home/cartlist',
        name:'CartList',
        component:CartList
      },
      {
        path:'/home/foodlist',
        name:'FoodList',
        component:FoodList
      },
      {
        path:'/home/orderlist',
        name:'OrderList',
        component:OrderList
      },
      {
        path:'/home/memberlist',
        name:'MemberList',
        component:MemberList
      },
      // 图表页面路由
      {
        path:'/home/imagelist',
        name:'ImageList',
        component:ImageList
      },
      // 文本编辑页面
      {
        path:'/home/updatelist',
        name:'UpdateList',
        component:UpdateList
      },
      // 添加商铺路由
      {
        path:'/home/addshop',
        name:'AddShop',
        component:AddShop
      },
      // 说明页面的路由
      {
        path:'/home/elplain',
        name:'Explain',
        component:Explain
      },
      // 添加商品页面的路由
      {
        path:'/home/addshoplist',
        name:'addshoplist',
        component:addshoplist
      }
    ]
  },
  {
    path: '/',
    name: 'Login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Login.vue')
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
