<template>
  <div class="user">
    <!-- 头部面包屑-->
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>数据管理</el-breadcrumb-item>
        <el-breadcrumb-item>用户列表列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 表格数据 -->
    <div class="bor-list">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="#" width="60" border>
        </el-table-column>
        <el-table-column prop="registe_time" label="注册日期">
        </el-table-column>
        <el-table-column prop="username" label="用户姓名"> </el-table-column>
        <el-table-column prop="city" label="注册地址"> </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="10"
        background
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {getUserCount} from '../api/userList'
export default {
  data() {
    return {
      tableData:[],
      offset: 0,
      limit: 10,
      total:0,
      currentPage: 1,
    };
  },
  created() {
      this.UserList()      
  },
  mounted() {
    this.initData()
  },
  methods: {
    // 用户列表的数据接口
    async UserList(){
      let {data:res} = await this.$http.get(`/v1/users/list?offset=${this.offset}&limit=${this.limit}`)
      this.tableData = res
    },
    // 分页
    handleSizeChange(size) {
      console.log(`每页${size}条`)
    },
    handleCurrentChange(page) {
      this.currentPage = page
      this.offset = (page -1)*this.limit
      this.UserList()
    },
    // 数据的总条数
    async initData(){
      try{
        let {data:CountData} = await getUserCount();
        // console.log(CountData)
        if(CountData.status == 1){
          this.total = CountData.count
          // console.log(total)
        }else{
          console.log('获取数据失败')
        }
        this.UserList()
      }catch(err){
        console.log('获取数据失败',err)
      }
    }
  },
};
</script>

<style scoped>
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.bor-list{
  padding: 20px;
}
.el-pagination{
  margin-top: 10px;
}
</style>
