<template>
  <div class="user">
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          >首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>数据管理</el-breadcrumb-item>
        <el-breadcrumb-item>管理员列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>首页</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 管理员数据列表 -->
    <div class="member-list">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="user_name" label="姓名">
        </el-table-column>
        <el-table-column prop="create_time" label="注册时间">
        </el-table-column>
        <el-table-column prop="city" label="地址"> </el-table-column>
        <el-table-column prop="admin" label="权限"> </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      background
      :page-size="10"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      offset: 0,
      limit: 10,
      total: 0,
      pageNum: 1,
    };
  },
  created() {
    this.getMemList();
    this.initMemData()
  },
  mounted() {},
  methods: {
    // 管理员列表数据
    async getMemList() {
      let { data: res } = await this.$http.get(`/admin/all?offset=${this.offset}&limit=${this.limit}`)
      console.log(res);
      this.tableData = res.data;
    },
    // 管理员列表总条数
    async initMemData(){
      try{
        let {data:countData} = await this.$http.get(`/admin/count`)
          if(countData.status == 1){
            this.total = countData.count
          }else{
            this.$message.error('获取数据失败')
          }
          this.getMemList()
      }catch(err){
        console.log('获取数据失败')
      }
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.offset = (val-1)*this.limit
      this.getMemList()
    }
  },
};
</script>

<style scoped>
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.member-list {
  padding: 20px;
}
.el-pagination{
  margin-top: 10px;
}
</style>
